.slick {
  margin: 0 !important;
  padding: 0 !important;
}

.App {
  text-align: center;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}

code {
  background: #fefefe;
  box-shadow: 0 0px 2px 0px #ccc;
  padding: 8px 16px;
  font-size: 14px;
  width: 100%;
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.wrapper {
  min-height: 80vh;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.setup.contracts {
  width: 100%;
  padding: 40px 0;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}
.setup.notice {
  margin: 15px 0;
}

.setup p {
  padding: 10px;
  background: #ededed;
  box-shadow: 0 1px 1px 2px #ccc;
  border: 1px solid #ddd;
  color: #444;
}
.setup.widgets {
  display: flex;
  max-width: 800px;
  width: 100%;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-start;
  margin: 20px 0;
}

.loader {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  padding: 40px 0;
}
h3 {
  margin-top: 30px;
}

.txt_c {
  text-align: center;
}

body {
  margin: 0;
  text-align: center;
}

.rotate_meme:first-of-type {
  background: url(/static/media/trump1.610fafed.png);
  -webkit-transform: rotate(-30deg) translateY(-150px) rotate(30deg);
          transform: rotate(-30deg) translateY(-150px) rotate(30deg);
  -webkit-animation: rotate-1 10s linear infinite;
          animation: rotate-1 10s linear infinite;
  background-position: 56% 100%;
  background-size: contain;
  background-repeat: no-repeat;
}

@-webkit-keyframes rotate-1 {
  from {
    -webkit-transform: rotate(-30deg) translateY(-150px) rotate(30deg);
            transform: rotate(-30deg) translateY(-150px) rotate(30deg);
  }
  to {
    -webkit-transform: rotate(330deg) translateY(-150px) rotate(-330deg);
            transform: rotate(330deg) translateY(-150px) rotate(-330deg);
  }
}

@keyframes rotate-1 {
  from {
    -webkit-transform: rotate(-30deg) translateY(-150px) rotate(30deg);
            transform: rotate(-30deg) translateY(-150px) rotate(30deg);
  }
  to {
    -webkit-transform: rotate(330deg) translateY(-150px) rotate(-330deg);
            transform: rotate(330deg) translateY(-150px) rotate(-330deg);
  }
}

